import React, { ButtonHTMLAttributes } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

type ButtonVariant = 'primary' | 'secondary' | 'outline' | 'text';
type ButtonSize = 'small' | 'medium' | 'large';

interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: ButtonVariant;
  size?: ButtonSize;
  icon?: React.ReactNode;
  href?: string;
  to?: string;
  fullWidth?: boolean;
}

const Button: React.FC<ButtonProps> = ({
  children,
  variant = 'primary',
  size = 'medium',
  icon,
  href,
  to,
  fullWidth = false,
  ...props
}) => {
  const buttonContent = (
    <>
      {icon && <IconWrapper>{icon}</IconWrapper>}
      <span>{children}</span>
    </>
  );

  if (to) {
    return (
      <ButtonLink
        to={to}
        $variant={variant}
        $size={size}
        $fullWidth={fullWidth}
      >
        {buttonContent}
      </ButtonLink>
    );
  }

  if (href) {
    return (
      <ButtonAnchor
        href={href}
        target="_blank"
        rel="noopener noreferrer"
        $variant={variant}
        $size={size}
        $fullWidth={fullWidth}
      >
        {buttonContent}
      </ButtonAnchor>
    );
  }

  return (
    <StyledButton
      $variant={variant}
      $size={size}
      $fullWidth={fullWidth}
      {...props}
    >
      {buttonContent}
    </StyledButton>
  );
};

interface StyledButtonProps {
  $variant: ButtonVariant;
  $size: ButtonSize;
  $fullWidth: boolean;
}

const buttonStyles = `
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-weight: 600;
  border-radius: 8px;
  transition: all 0.3s ease;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  box-shadow: 0 2px 6px var(--shadow);

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px var(--shadow);
  }

  &:active {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
  }
`;

const getVariantStyles = (variant: ButtonVariant) => {
  switch (variant) {
    case 'primary':
      return `
        background-color: var(--accent);
        color: white;
        border: none;
        
        &:hover {
          background-color: var(--accent-hover);
        }
      `;
    case 'secondary':
      return `
        background-color: var(--bg-secondary);
        color: var(--text-primary);
        border: none;
        
        &:hover {
          background-color: var(--bg-primary);
        }
      `;
    case 'outline':
      return `
        background-color: transparent;
        color: var(--accent);
        border: 2px solid var(--accent);
        
        &:hover {
          background-color: var(--accent);
          color: white;
        }
      `;
    case 'text':
      return `
        background-color: transparent;
        color: var(--accent);
        border: none;
        box-shadow: none;
        
        &:hover {
          color: var(--accent-hover);
          background-color: rgba(77, 159, 255, 0.1);
        }
      `;
    default:
      return '';
  }
};

const getSizeStyles = (size: ButtonSize) => {
  switch (size) {
    case 'small':
      return `
        padding: 0.5rem 1rem;
        font-size: 0.875rem;
      `;
    case 'medium':
      return `
        padding: 0.75rem 1.5rem;
        font-size: 1rem;
      `;
    case 'large':
      return `
        padding: 1rem 2rem;
        font-size: 1.125rem;
      `;
    default:
      return '';
  }
};

const StyledButton = styled.button<StyledButtonProps>`
  ${buttonStyles}
  ${({ $variant }) => getVariantStyles($variant)}
  ${({ $size }) => getSizeStyles($size)}
  width: ${({ $fullWidth }) => ($fullWidth ? '100%' : 'auto')};
`;

const ButtonLink = styled(Link)<StyledButtonProps>`
  ${buttonStyles}
  ${({ $variant }) => getVariantStyles($variant)}
  ${({ $size }) => getSizeStyles($size)}
  width: ${({ $fullWidth }) => ($fullWidth ? '100%' : 'auto')};
`;

const ButtonAnchor = styled.a<StyledButtonProps>`
  ${buttonStyles}
  ${({ $variant }) => getVariantStyles($variant)}
  ${({ $size }) => getSizeStyles($size)}
  width: ${({ $fullWidth }) => ($fullWidth ? '100%' : 'auto')};
`;

const IconWrapper = styled.span`
  display: flex;
  align-items: center;
  justify-content: center;
`;

export default Button; 