import React from 'react';
import ReactDOM from 'react-dom/client';
import { Analytics } from '@vercel/analytics/react';
import App from './App';
import reportWebVitals from './reportWebVitals';
import './index.css';

// Add viewport meta tag dynamically for better mobile performance
const setViewportForMobile = () => {
  // Check if viewport meta tag already exists
  let viewport = document.querySelector('meta[name="viewport"]');
  
  // If it doesn't exist, create it
  if (!viewport) {
    viewport = document.createElement('meta');
    (viewport as HTMLMetaElement).name = 'viewport';
    document.head.appendChild(viewport);
  }
  
  // Set optimal viewport properties for animation performance
  viewport.setAttribute('content', 
    'width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no, viewport-fit=cover');
};

// Call before rendering
setViewportForMobile();

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <App />
    <Analytics />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
