import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { Link, useLocation } from 'react-router-dom';
import ThemeSwitcher from './ThemeSwitcher';
import { FiMenu, FiX, FiDownload } from 'react-icons/fi';
import Icon from './Icon';
import Button from './Button';

const Header: React.FC = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const location = useLocation();

  // Handle scroll event to change header style
  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 20) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  // Add useEffect to close mobile menu on route change
  useEffect(() => {
    setIsMobileMenuOpen(false);
  }, [location.pathname]);

  const toggleMobileMenu = () => setIsMobileMenuOpen(!isMobileMenuOpen);
  const closeMobileMenu = () => setIsMobileMenuOpen(false);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  // Function to check if a nav item is active
  const isActive = (path: string) => {
    // Exact match for home page
    if (path === '/' && location.pathname === '/') {
      return true;
    }
    // For other pages, check if the pathname starts with the path
    // This ensures /projects will match even if there are query params or subpaths
    return path !== '/' && location.pathname.startsWith(path);
  };

  const navItems = [
    { name: 'Home', path: '/' },
    { name: 'About', path: '/about' },
    { name: 'Projects', path: '/projects' },
    { name: 'Skills', path: '/skills' },
    { name: 'Blog', path: '/blog' },
    { name: 'Contact', path: '/contact' },
  ];

  return (
    <HeaderContainer isScrolled={isScrolled}>
      <HeaderContent>
        <Logo
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <LogoLink 
            to="/" 
            onClick={(e) => {
              // Only scroll to top if we're already on the home page
              if (window.location.pathname === '/') {
                e.preventDefault();
                window.scrollTo({
                  top: 0,
                  behavior: 'smooth'
                });
              }
              // Otherwise, let the normal navigation happen
            }}
          >
            SH
          </LogoLink>
        </Logo>

        {/* Desktop Navigation */}
        <DesktopNav>
          <NavList>
            {navItems.map((item, index) => (
              <NavItem
                key={item.name}
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: 0.1 * index }}
              >
                <NavLink 
                  to={item.path} 
                  onClick={closeMobileMenu}
                  $isActive={isActive(item.path)}
                >
                  {item.name}
                </NavLink>
              </NavItem>
            ))}
          </NavList>
        </DesktopNav>

        <RightSection>
          <ButtonWrapper
            as={motion.div}
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <DownloadAnchor 
              href="/files/Suren's Resume - Frontend Software Engineer.pdf" 
              download="Suren's Resume - Frontend Software Engineer.pdf"
            >
              <Button 
                size="small"
                variant="primary"
                icon={<FiDownload size={16} />}
              >
                Download CV
              </Button>
            </DownloadAnchor>
          </ButtonWrapper>
          <ThemeSwitcher />
          <MobileMenuToggle onClick={toggleMobileMenu}>
            {isMobileMenuOpen ? <Icon icon={FiX} size={24} /> : <Icon icon={FiMenu} size={24} />}
          </MobileMenuToggle>
        </RightSection>

        {/* Mobile Navigation */}
        <AnimatePresence>
          {isMobileMenuOpen && (
            <MobileNav
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              <MobileNavList>
                {navItems.map((item, index) => (
                  <MobileNavItem
                    key={item.name}
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.3, delay: 0.05 * index }}
                  >
                    <NavLink 
                      to={item.path} 
                      onClick={closeMobileMenu}
                      $isActive={isActive(item.path)}
                    >
                      {item.name}
                    </NavLink>
                  </MobileNavItem>
                ))}
                <MobileNavItem
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.3, delay: 0.25 }}
                >
                  <DownloadAnchor 
                    href="/files/Suren's Resume - Frontend Software Engineer.pdf" 
                    download="Suren's Resume - Frontend Software Engineer.pdf"
                  >
                    <Button 
                      size="small"
                      variant="primary"
                      icon={<FiDownload size={16} />}
                      fullWidth
                    >
                      Download CV
                    </Button>
                  </DownloadAnchor>
                </MobileNavItem>
              </MobileNavList>
            </MobileNav>
          )}
        </AnimatePresence>
      </HeaderContent>
    </HeaderContainer>
  );
};

interface HeaderContainerProps {
  isScrolled: boolean;
}

const HeaderContainer = styled.header<HeaderContainerProps>`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: ${({ isScrolled }) => (isScrolled ? '0.6rem 0' : '1.5rem 0')};
  background-color: ${({ isScrolled }) => (isScrolled ? 'var(--bg-secondary)' : 'transparent')};
  backdrop-filter: ${({ isScrolled }) => (isScrolled ? 'blur(10px)' : 'none')};
  box-shadow: ${({ isScrolled }) => (isScrolled ? '0 2px 10px var(--shadow)' : 'none')};
  z-index: 1000;
  transition: all 0.3s ease;
`;

const HeaderContent = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
`;

const Logo = styled(motion.div)`
  font-size: 1.8rem;
  font-weight: 700;
`;

const LogoLink = styled(Link)`
  color: var(--text-primary);
  text-decoration: none;
  cursor: pointer;
  
  &:hover {
    color: var(--accent);
  }
`;

const DesktopNav = styled.nav`
  @media (max-width: 768px) {
    display: none;
  }
`;

const NavList = styled.ul`
  display: flex;
  list-style: none;
  gap: 2rem;
`;

const NavItem = styled(motion.li)``;

interface NavLinkProps {
  $isActive: boolean;
}

const NavLink = styled(Link)<NavLinkProps>`
  color: ${props => props.$isActive ? 'var(--accent)' : 'var(--text-primary)'};
  font-weight: ${props => props.$isActive ? '600' : '500'};
  text-decoration: none;
  padding: 0.5rem;
  position: relative;
  
  &:after {
    content: '';
    position: absolute;
    width: ${props => props.$isActive ? '100%' : '0'};
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: var(--accent);
    transition: width 0.3s ease;
  }
  
  &:hover {
    color: var(--accent);
    
    &:after {
      width: 100%;
    }
  }
`;

const RightSection = styled.div`
  display: flex;
  align-items: center;
  gap: 1rem;
`;

const ButtonWrapper = styled(motion.div)`
  @media (max-width: 768px) {
    display: none;
  }
`;

const DownloadAnchor = styled.a`
  text-decoration: none;
  display: block;
  
  &:hover span {
    color: white !important;
  }
`;

const MobileMenuToggle = styled.button`
  display: none;
  background: none;
  border: none;
  color: var(--text-primary);
  cursor: pointer;
  
  @media (max-width: 768px) {
    display: block;
  }
`;

const MobileNav = styled(motion.nav)`
  display: none;
  
  @media (max-width: 768px) {
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: var(--bg-secondary);
    box-shadow: 0 4px 12px var(--shadow);
    border-radius: 0 0 8px 8px;
    padding: 1rem;
    z-index: 1000;
  }
`;

const MobileNavList = styled.ul`
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
`;

const MobileNavItem = styled(motion.li)`
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--border);
  
  &:last-child {
    border-bottom: none;
  }
`;

export default Header;