import React, { useState } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { FiSettings, FiX, FiRefreshCw, FiZap, FiCheck, FiChevronLeft, FiChevronRight } from 'react-icons/fi';
import { useTheme, CustomThemeProperties } from '../context/ThemeContext';
import { generateThemeFromKeywords } from '../services/aiService';
import Icon from './Icon';
import Button from './Button';

const ResponsiveIcon = styled(Icon)`
  @media (max-width: 768px) {
    svg {
      width: 20px;
      height: 20px;
    }
  }
`;

const ThemeGenerator: React.FC = () => {
  const { theme, setCustomTheme, resetToDefaultTheme, customTheme } = useTheme();
  const [isOpen, setIsOpen] = useState(false);
  const [keywords, setKeywords] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [generatedThemes, setGeneratedThemes] = useState<CustomThemeProperties[]>([]);
  const [selectedThemeIndex, setSelectedThemeIndex] = useState(0);

  const toggleGenerator = () => {
    setIsOpen(!isOpen);
    // Reset state when closing
    if (isOpen) {
      setKeywords('');
      setError(null);
      setGeneratedThemes([]);
      setSelectedThemeIndex(0);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setKeywords(e.target.value);
    setError(null);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (keywords.trim() === '') {
      setError('Please enter some keywords to generate a theme.');
      return;
    }
    
    setIsLoading(true);
    setError(null);
    setGeneratedThemes([]);
    setSelectedThemeIndex(0);
    
    try {
      const themes = await generateThemeFromKeywords(keywords);
      setGeneratedThemes(themes);
    } catch (err) {
      setError('Failed to generate theme. Please try again with different keywords.');
      console.error('Theme generation error:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const applySelectedTheme = () => {
    if (generatedThemes.length > 0) {
      setCustomTheme(generatedThemes[selectedThemeIndex]);
      setIsOpen(false);
      // Reset state
      setKeywords('');
      setGeneratedThemes([]);
      setSelectedThemeIndex(0);
    }
  };

  const resetTheme = () => {
    resetToDefaultTheme();
    setIsOpen(false);
  };

  const nextTheme = () => {
    setSelectedThemeIndex((prevIndex) => 
      prevIndex === generatedThemes.length - 1 ? 0 : prevIndex + 1
    );
  };

  const prevTheme = () => {
    setSelectedThemeIndex((prevIndex) => 
      prevIndex === 0 ? generatedThemes.length - 1 : prevIndex - 1
    );
  };

  const getPreviewStyle = (themeProps: CustomThemeProperties) => ({
    background: `linear-gradient(135deg, ${themeProps.bgPrimary} 0%, ${themeProps.bgSecondary} 100%)`,
    color: themeProps.textPrimary,
    borderColor: themeProps.border,
    fontFamily: themeProps.fontFamily,
  });

  return (
    <>
      <GeneratorToggle
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.95 }}
        onClick={toggleGenerator}
        $isCustomTheme={theme === 'custom'}
      >
        <ResponsiveIcon icon={isOpen ? FiX : FiSettings} size={24} />
      </GeneratorToggle>
      
      <AnimatePresence>
        {isOpen && (
          <GeneratorContainer
            initial={{ opacity: 0, y: 20, scale: 0.9 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 20, scale: 0.9 }}
            transition={{ duration: 0.3 }}
          >
            <GeneratorHeader>
              <GeneratorTitle>Theme Generator</GeneratorTitle>
              <CloseButton onClick={toggleGenerator}>
                <Icon icon={FiX} size={20} />
              </CloseButton>
            </GeneratorHeader>
            
            <GeneratorContent>
              <Description>
                Enter a few keywords (e.g., "cyberpunk, dark, electric") and let AI generate custom themes for you.
              </Description>
              
              <GeneratorForm onSubmit={handleSubmit}>
                <GeneratorInput
                  type="text"
                  placeholder="Enter keywords (e.g., cyberpunk, dark, electric)"
                  value={keywords}
                  onChange={handleInputChange}
                  disabled={isLoading}
                />
                <GenerateButton
                  type="submit"
                  disabled={isLoading || keywords.trim() === ''}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  {isLoading ? (
                    <LoadingIcon icon={FiRefreshCw} size={20} />
                  ) : (
                    <>
                      <Icon icon={FiZap} size={20} />
                      <span>Generate</span>
                    </>
                  )}
                </GenerateButton>
              </GeneratorForm>
              
              {error && <ErrorMessage>{error}</ErrorMessage>}
              
              {generatedThemes.length > 0 && (
                <ThemePreviewContainer>
                  <ThemePreviewHeader>
                    <ThemePreviewTitle>
                      Preview {generatedThemes.length > 1 && `(${selectedThemeIndex + 1}/${generatedThemes.length})`}
                    </ThemePreviewTitle>
                    {generatedThemes.length > 1 && (
                      <PreviewNavigation>
                        <NavButton onClick={prevTheme}>
                          <Icon icon={FiChevronLeft} size={18} />
                        </NavButton>
                        <NavButton onClick={nextTheme}>
                          <Icon icon={FiChevronRight} size={18} />
                        </NavButton>
                      </PreviewNavigation>
                    )}
                  </ThemePreviewHeader>
                  <ThemePreview style={getPreviewStyle(generatedThemes[selectedThemeIndex])}>
                    <PreviewTitle>Sample Title</PreviewTitle>
                    <PreviewText>This is how your theme will look. Check text readability and color contrast.</PreviewText>
                    <PreviewButton style={{ backgroundColor: generatedThemes[selectedThemeIndex].accent, color: '#fff' }}>
                      Button
                    </PreviewButton>
                  </ThemePreview>
                  <ActionButtons>
                    <ApplyButton 
                      onClick={applySelectedTheme}
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                    >
                      <Icon icon={FiCheck} size={16} />
                      <span>Apply Theme</span>
                    </ApplyButton>
                    <TryAgainButton
                      onClick={() => setGeneratedThemes([])}
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                    >
                      <Icon icon={FiRefreshCw} size={16} />
                      <span>Try Again</span>
                    </TryAgainButton>
                  </ActionButtons>
                </ThemePreviewContainer>
              )}
              
              {theme === 'custom' && (
                <ResetButton 
                  onClick={resetTheme}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Reset to Default Theme
                </ResetButton>
              )}
            </GeneratorContent>
          </GeneratorContainer>
        )}
      </AnimatePresence>
    </>
  );
};

interface GeneratorToggleProps {
  $isCustomTheme: boolean;
}

const GeneratorToggle = styled(motion.button)<GeneratorToggleProps>`
  position: fixed;
  bottom: 20px;
  right: 90px; /* Position to the left of ChatBot button (20px + 60px + 10px gap) */
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: ${props => props.$isCustomTheme ? 'var(--accent)' : 'var(--bg-secondary)'};
  color: ${props => props.$isCustomTheme ? 'white' : 'var(--text-primary)'};
  border: 2px solid ${props => props.$isCustomTheme ? 'transparent' : 'var(--border)'};
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 4px 12px var(--shadow);
  z-index: 999;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
  
  &:hover {
    background-color: ${props => props.$isCustomTheme ? 'var(--accent-hover)' : 'var(--bg-primary)'};
  }
  
  @media (max-width: 768px) {
    width: 48px;
    height: 48px;
    bottom: 16px;
    right: 72px; /* 16px + 48px + 8px gap */
    border-width: 1px;
  }
`;

