import React, { useState, useRef, useEffect, useCallback } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { FiMessageSquare, FiX, FiSend, FiUser, FiCpu, FiRefreshCw, FiThumbsUp, FiThumbsDown, FiChevronDown } from 'react-icons/fi';
import Icon from './Icon';
import Button from './Button';
import { searchKnowledge, resetConversationContext } from '../context/ChatbotKnowledgeBase';

// Improved AI response function with message history context
const getBotResponse = (message: string, messageHistory: Message[]): Promise<string> => {
  // Extract recent conversation context to provide to the knowledge base
  const recentMessages = messageHistory.slice(-5).map(msg => msg.text);
  
  // Simulate network request with a delay
  return new Promise((resolve) => {
    setTimeout(() => {
      // Use our enhanced knowledge base to find relevant responses with context
      const response = searchKnowledge(message);
      resolve(response);
    }, 600 + Math.random() * 600); // Slightly faster, more responsive feel
  });
};

interface Message {
  id: number;
  text: string;
  sender: 'user' | 'bot';
  timestamp: Date;
  feedback?: 'positive' | 'negative' | null;
}

// Add this component for responsive icon sizing
const ResponsiveIcon = styled(Icon)`
  @media (max-width: 768px) {
    svg {
      width: 20px;
      height: 20px;
    }
  }
`;

const MAX_MESSAGES = 50; // Maximum messages to keep in memory

const ChatBot: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([
    {
      id: 1,
      text: "Hi there! I'm an AI assistant that can answer questions about Suren Hakobyan's skills, projects, and experience. How can I help you today?",
      sender: 'bot',
      timestamp: new Date()
    }
  ]);
  const [inputValue, setInputValue] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [interactionCount, setInteractionCount] = useState(0);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  const [userInterests, setUserInterests] = useState<Record<string, number>>({});
  const [isAtBottom, setIsAtBottom] = useState(true);
  const [hasNewMessages, setHasNewMessages] = useState(false);

  // Memory management - trim older messages when hitting the limit
  const trimOldMessages = useCallback((currentMessages: Message[]): Message[] => {
    if (currentMessages.length > MAX_MESSAGES) {
      // Keep the first welcome message and the most recent messages
      return [
        currentMessages[0],
        ...currentMessages.slice(currentMessages.length - MAX_MESSAGES + 1)
      ];
    }
    return currentMessages;
  }, []);

  const toggleChat = () => {
    setIsOpen(!isOpen);
    
    // If opening chat after a while, add a re-engagement message
    if (!isOpen && messages.length > 1 && 
        (new Date().getTime() - messages[messages.length - 1].timestamp.getTime()) > 300000) {
      setTimeout(() => {
        const reengagementMessage: Message = {
          id: messages.length + 1,
          text: "Welcome back! Is there anything else you'd like to know about Suren?",
          sender: 'bot',
          timestamp: new Date()
        };
        setMessages(prev => [...prev, reengagementMessage]);
      }, 500);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);
  };

  const resetConversation = () => {
    resetConversationContext();
    setMessages([
      {
        id: 1,
        text: "Hi there! I'm an AI assistant that can answer questions about Suren Hakobyan's skills, projects, and experience. How can I help you today?",
        sender: 'bot',
        timestamp: new Date()
      }
    ]);
    setInteractionCount(0);
    setUserInterests({});
  };

  // Track interests from user queries to improve future suggestions
  const updateUserInterests = (text: string) => {
    const lowerText = text.toLowerCase();
    const interestKeywords = {
      'skills': ['skill', 'technology', 'tech', 'frontend', 'javascript', 'react', 'vue'],
      'projects': ['project', 'work', 'portfolio', 'build', 'application'],
      'experience': ['experience', 'job', 'work', 'company', 'position'],
      'education': ['education', 'degree', 'university', 'school', 'learn'],
      'personal': ['personal', 'hobby', 'interest', 'life', 'about']
    };
    
    // Check for keywords in user message
    Object.entries(interestKeywords).forEach(([category, keywords]) => {
      if (keywords.some(keyword => lowerText.includes(keyword))) {
        setUserInterests(prev => ({
          ...prev,
          [category]: (prev[category] || 0) + 1
        }));
      }
    });
  };

  const handleSendMessage = async (text: string) => {
    if (text.trim() === '') return;
    
    const userMessage: Message = {
      id: messages.length + 1,
      text: text,
      sender: 'user',
      timestamp: new Date()
    };
    
    setMessages(prev => trimOldMessages([...prev, userMessage]));
    setInputValue('');
    setIsTyping(true);
    setInteractionCount(prev => prev + 1);
    setHasNewMessages(true);
    
    // Track user interests based on their query
    updateUserInterests(text);
    
    try {
      // Pass message history for better context
      const response = await getBotResponse(text, messages);
      
      const botMessage: Message = {
        id: messages.length + 2,
        text: response,
        sender: 'bot',
        timestamp: new Date()
      };
      
      setMessages(prev => trimOldMessages([...prev, botMessage]));
      
      // After several interactions, occasionally ask for feedback
      if (interactionCount % 4 === 0) {
        setTimeout(() => {
          const feedbackMessage: Message = {
            id: messages.length + 3,
            text: "Is this information helpful to you?",
            sender: 'bot',
            timestamp: new Date()
          };
          setMessages(prev => [...prev, feedbackMessage]);
        }, 1000);
      }
    } catch (error) {
      const errorMessage: Message = {
        id: messages.length + 2,
        text: "Sorry, I'm having trouble connecting right now. Please try again later.",
        sender: 'bot',
        timestamp: new Date()
      };
      
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsTyping(false);
    }
  };

  const handleFeedback = (messageId: number, type: 'positive' | 'negative') => {
    // Update the message with feedback
    setMessages(prev => prev.map(msg => 
      msg.id === messageId ? { ...msg, feedback: type } : msg
    ));
    
    // If negative feedback, offer to clarify
    if (type === 'negative') {
      setTimeout(() => {
        const followupMessage: Message = {
          id: messages.length + 1,
          text: "I'm sorry that wasn't helpful. Could you clarify what you're looking for?",
          sender: 'bot',
          timestamp: new Date()
        };
        setMessages(prev => [...prev, followupMessage]);
      }, 500);
    } else {
      // For positive feedback
      setTimeout(() => {
        const followupMessage: Message = {
          id: messages.length + 1,
          text: "Great! Is there anything else you'd like to know?",
          sender: 'bot',
          timestamp: new Date()
        };
        setMessages(prev => [...prev, followupMessage]);
      }, 500);
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    handleSendMessage(inputValue);
  };
  
  // Helper function to extract topics from recent messages
  const extractTopicsFromMessages = (recentMsgs: Message[]): string[] => {
    const topics: string[] = [];
    const keywords = {
      'simplified': ['simplified', 'photo editor', 'video editor', 'ai editor'],
      'picsart': ['picsart', 'creative editor', 'konva'],
      'jobleads': ['jobleads', 'job search', 'resume'],
      'technology': ['react', 'vue', 'javascript', 'typescript', 'canvas'],
      'project': ['project', 'application', 'website', 'editor'],
      'education': ['education', 'university', 'degree', 'yerevan']
    };
    
    recentMsgs.forEach(msg => {
      const lowerText = msg.text.toLowerCase();
      
      Object.entries(keywords).forEach(([topic, words]) => {
        if (words.some(word => lowerText.includes(word)) && !topics.includes(topic)) {
          topics.push(topic);
        }
      });
    });
    
    return topics;
  };

  // Handle scroll events to detect if user is at the bottom
  const handleScroll = (e: React.UIEvent<HTMLDivElement>) => {
    const { scrollTop, scrollHeight, clientHeight } = e.currentTarget;
    const isBottom = Math.abs(scrollHeight - scrollTop - clientHeight) < 10;
    setIsAtBottom(isBottom);
    
    if (isBottom && hasNewMessages) {
      setHasNewMessages(false);
    }
  };
  
  // Scroll to bottom when new messages arrive and user was at bottom
  useEffect(() => {
    if (isAtBottom) {
      messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    } else if (messages.length > 0 && messages[messages.length - 1].sender === 'bot') {
      setHasNewMessages(true);
    }
  }, [messages, isAtBottom]);

  // Focus input when chat opens
  useEffect(() => {
    if (isOpen) {
      inputRef.current?.focus();
    }
  }, [isOpen]);

  return (
    <>
      <ChatBotToggle
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.95 }}
        onClick={toggleChat}
      >
        <ResponsiveIcon icon={isOpen ? FiX : FiMessageSquare} size={24} />
      </ChatBotToggle>
      
      <AnimatePresence>
        {isOpen && (
          <ChatContainer
            initial={{ opacity: 0, y: 20, scale: 0.9 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 20, scale: 0.9 }}
            transition={{ duration: 0.3 }}
          >
            <ChatHeader>
              <ChatTitle>AI Assistant</ChatTitle>
              <MessageCount>{messages.length > 1 ? `${messages.length - 1} messages` : ''}</MessageCount>
              <HeaderActions>
                <ResetButton 
                  onClick={resetConversation}
                  title="Reset conversation"
                >
                  <Icon icon={FiRefreshCw} size={18} />
                </ResetButton>
                <CloseButton onClick={toggleChat}>
                  <Icon icon={FiX} size={20} />
                </CloseButton>
              </HeaderActions>
            </ChatHeader>
            
            <MessagesContainer onScroll={handleScroll}>
              {messages.map(message => (
                <MessageBubble
                  key={message.id}
                  $sender={message.sender}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                >
                  <MessageIcon>
                    <Icon icon={message.sender === 'user' ? FiUser : FiCpu} size={16} />
                  </MessageIcon>
                  <MessageContentWrapper>
                    <MessageContent>{formatMessage(message.text)}</MessageContent>
                    
                    {message.sender === 'bot' && message.text !== "Is this information helpful to you?" && (
                      <FeedbackButtons>
                        {message.feedback ? (
                          <FeedbackMessage>
                            {message.feedback === 'positive' ? 'Thanks for the feedback!' : 'Thanks for letting me know'}
                          </FeedbackMessage>
                        ) : (
                          <>
                            <FeedbackButton 
                              onClick={() => handleFeedback(message.id, 'positive')}
                              title="This was helpful"
                            >
                              <Icon icon={FiThumbsUp} size={12} />
                            </FeedbackButton>
                            <FeedbackButton 
                              onClick={() => handleFeedback(message.id, 'negative')}
                              title="This wasn't helpful"
                            >
                              <Icon icon={FiThumbsDown} size={12} />
                            </FeedbackButton>
                          </>
                        )}
                      </FeedbackButtons>
                    )}
                  </MessageContentWrapper>
                </MessageBubble>
              ))}
              
              {isTyping && (
                <MessageBubble
                  $sender="bot"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                >
                  <MessageIcon>
                    <Icon icon={FiCpu} size={16} />
                  </MessageIcon>
                  <TypingIndicator>
                    <TypingDot delay={0} />
                    <TypingDot delay={0.2} />
                    <TypingDot delay={0.4} />
                  </TypingIndicator>
                </MessageBubble>
              )}
              
              <div ref={messagesEndRef} />
            </MessagesContainer>
            
            {/* New scroll to bottom button when new messages arrive */}
            {hasNewMessages && !isAtBottom && (
              <ScrollToBottomButton
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.95 }}
                onClick={() => {
                  messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
                  setHasNewMessages(false);
                }}
              >
                <Icon icon={FiChevronDown} size={20} />
              </ScrollToBottomButton>
            )}
            
            <ChatForm onSubmit={handleSubmit}>
              <ChatInput
                type="text"
                placeholder="Type your message..."
                value={inputValue}
                onChange={handleInputChange}
                ref={inputRef}
                disabled={isTyping}
              />
              <SendButton
                type="submit"
                disabled={inputValue.trim() === '' || isTyping}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Icon icon={FiSend} size={20} />
              </SendButton>
            </ChatForm>
            
            {/* Loading indicator */}
            {isTyping && (
              <LoadingIndicator 
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
              >
                AI is thinking...
              </LoadingIndicator>
            )}
          </ChatContainer>
        )}
      </AnimatePresence>
    </>
  );
};

