// Enhanced helper function to format text with markdown-like syntax and links
const formatMessage = (text: string): React.ReactNode => {
  if (!text.includes('**') && !text.includes('http')) {
    return text;
  }
  
  // First handle bold text
  let parts = text.split(/(\*\*.*?\*\*)/g);
  
  const formattedParts = parts.map((part, index) => {
    if (part.startsWith('**') && part.endsWith('**')) {
      // Extract the text between ** and make it bold
      const boldText = part.substring(2, part.length - 2);
      return <strong key={`bold-${index}`}>{boldText}</strong>;
    }
    
    // Check for URLs in the remaining text
    if (part.includes('http')) {
      // Simple regex to find URLs
      const urlRegex = /(https?:\/\/[^\s]+)/g;
      const subParts = part.split(urlRegex);
      
      return subParts.map((subPart, subIndex) => {
        if (urlRegex.test(subPart)) {
          return (
            <a 
              key={`link-${index}-${subIndex}`} 
              href={subPart} 
              target="_blank" 
              rel="noopener noreferrer"
              style={{ color: 'var(--accent)', textDecoration: 'underline' }}
            >
              {subPart}
            </a>
          );
        }
        return subPart;
      });
    }
    
    return part;
  });
  
  // Flatten the array to handle nested arrays from URL formatting
  return formattedParts.flat();
};

export default ChatBot; 