// Function to format array as a readable list
const formatList = (items: string[]): string => {
  if (items.length === 0) return "";
  if (items.length === 1) return items[0];
  if (items.length === 2) return `${items[0]} and ${items[1]}`;
  
  const lastItem = items[items.length - 1];
  const otherItems = items.slice(0, -1).join(", ");
  return `${otherItems}, and ${lastItem}`;
};

// Calculate similarity score between two texts (enhanced semantic matching)
const calculateSimilarity = (text1: string, text2: string): number => {
  const words1 = text1.toLowerCase().split(/\s+/);
  const words2 = text2.toLowerCase().split(/\s+/);
  
  const uniqueWords = new Set([...words1, ...words2]);
  let matchCount = 0;
  let wordImportance = 0;
  
  // Convert Set to Array to avoid iteration issues
  for (const word of Array.from(uniqueWords)) {
    // Skip common stop words that have little semantic value
    if (['the', 'a', 'an', 'and', 'or', 'but', 'in', 'on', 'at', 'to', 'for', 'with', 'is', 'are', 'was', 'were'].includes(word)) {
      continue;
    }
    
    const isInText1 = words1.includes(word);
    const isInText2 = words2.includes(word);
    
    if (isInText1 && isInText2) {
      // Core vocabulary matches are weighted higher
      const importance = isTopicKeyword(word) ? 3 : 1;
      matchCount += importance;
      wordImportance += importance;
    } else {
      wordImportance += isTopicKeyword(word) ? 2 : 0.5;
    }
  }
  
  // Improved Jaccard similarity with weighted importance
  return matchCount / (wordImportance || 1);
};

// Check if word is an important topic keyword
const isTopicKeyword = (word: string): boolean => {
  const allKeywords = Object.values(chatbotKnowledge.topics).flat();
  return allKeywords.includes(word);
};

// Identify topic from query text (with improved matching)
const identifyTopics = (query: string): string[] => {
  const lowerQuery = query.toLowerCase();
  const foundTopics: string[] = [];
  const topicScores: Record<string, number> = {};
  
  // Score each topic based on keyword matches
  for (const [topic, keywords] of Object.entries(chatbotKnowledge.topics)) {
    topicScores[topic] = 0;
    
    for (const keyword of keywords) {
      if (lowerQuery.includes(keyword)) {
        // Weight by keyword length (longer keywords are more specific)
        topicScores[topic] += keyword.length / 3;
      }
    }
  }
  
  // Sort topics by score and take top 2
  const sortedTopics = Object.entries(topicScores)
    .filter(([_, score]) => score > 0)
    .sort(([_, scoreA], [__, scoreB]) => scoreB - scoreA)
    .map(([topic]) => topic);
  
  return sortedTopics.slice(0, 2);
};

interface ConversationContext {
  recentTopics: string[];
  recentQuestions: string[];
  lastResponses: string[];
  sessionTopics: Record<string, number>; // Track frequency of topics in session
  userPreferences: string[]; // Track user's interests based on questions
  conversationStart: Date;
}

// Enhanced global context to track conversation state
let conversationContext: ConversationContext = {
  recentTopics: [],
  recentQuestions: [],
  lastResponses: [],
  sessionTopics: {},
  userPreferences: [],
  conversationStart: new Date()
};

// Reset conversation context
export const resetConversationContext = (): void => {
  conversationContext = {
    recentTopics: [],
    recentQuestions: [],
    lastResponses: [],
    sessionTopics: {},
    userPreferences: [],
    conversationStart: new Date()
  };
};

// Update conversation context with enhanced tracking
const updateContext = (query: string, response: string): void => {
  // Keep track of last 5 questions for better context
  conversationContext.recentQuestions = [
    query,
    ...conversationContext.recentQuestions.slice(0, 4)
  ];
  
  // Keep track of last 3 responses
  conversationContext.lastResponses = [
    response,
    ...conversationContext.lastResponses.slice(0, 2)
  ];
  
  // Update topics with improved handling
  const newTopics = identifyTopics(query);
  
  // Track session-wide topic frequencies
  newTopics.forEach(topic => {
    conversationContext.sessionTopics[topic] = (conversationContext.sessionTopics[topic] || 0) + 1;
    
    // Add to user preferences if asked about multiple times
    if (conversationContext.sessionTopics[topic] >= 2 && 
        !conversationContext.userPreferences.includes(topic)) {
      conversationContext.userPreferences.push(topic);
    }
  });
  
  const topicsSet = new Set([...newTopics, ...conversationContext.recentTopics]);
  
  // Convert Set to Array to avoid iteration issues
  conversationContext.recentTopics = Array.from(topicsSet).slice(0, 5);
};

// Improved detection of follow-up questions
const isFollowUpQuestion = (query: string): boolean => {
  const lowerQuery = query.toLowerCase();
  
  // Enhanced set of follow-up indicators
  const followUpIndicators = [
    "what about", "how about", "and", "also", "too", "as well",
    "more", "elaborate", "explain", "tell me more", "details",
    "why", "how", "when", "where", "which", "who", "what",
    "can you", "could you", "would", "do you", "have you",
    "specifically", "exactly", "for instance", "example"
  ];
  
  // Check for pronouns that likely refer to previous content
  const pronounIndicators = ["it", "that", "this", "these", "those", "they", "them"];
  
  // Check for short queries which are often follow-ups
  if (lowerQuery.split(' ').length <= 3) {
    return true;
  }
  
  // Check for pronouns at the beginning of the query
  for (const pronoun of pronounIndicators) {
    if (lowerQuery.startsWith(pronoun + " ") || lowerQuery.startsWith(pronoun + "?")) {
      return true;
    }
  }
  
  for (const indicator of followUpIndicators) {
    if (lowerQuery.includes(indicator)) {
      return true;
    }
  }
  
  // Check if query only contains a topic word without context
  const hasTopicWordOnly = Object.values(chatbotKnowledge.topics)
    .some(keywords => keywords.some(keyword => 
      lowerQuery === keyword || lowerQuery === keyword + "?"
    ));
  
  return hasTopicWordOnly;
};

// Enhanced process for follow-up questions with better context understanding
const processFollowUp = (query: string): string | null => {
  const lowerQuery = query.toLowerCase();
  
  // Enhanced approach using conversation history
  if (conversationContext.recentQuestions.length > 1) {
    const previousQuery = conversationContext.recentQuestions[1];
    const combinedQuery = `${previousQuery} and ${query}`;
    
    // Try topics from previous query first
    const previousTopics = identifyTopics(previousQuery);
    for (const topic of previousTopics) {
      const response = searchSpecificTopic(combinedQuery, topic);
      if (response) {
        return response;
      }
    }
  }
  
  // If there are recent topics, check if the query relates to them
  if (conversationContext.recentTopics.length > 0) {
    for (const topic of conversationContext.recentTopics) {
      // Craft a more specific query by combining the follow-up with the topic
      const enhancedQuery = `${query} ${topic}`;
      
      // Try to get a response for the enhanced query
      const response = searchSpecificTopic(enhancedQuery, topic);
      if (response) {
        return response;
      }
    }
  }
  
  // If we have a last response, check if the follow-up is related to it
  if (conversationContext.lastResponses.length > 0) {
    if (calculateSimilarity(query, conversationContext.lastResponses[0]) > 0.2) {
      // The follow-up is related to the last response
      // Provide more details or clarify the previous response
      const mostFrequentTopic = Object.entries(conversationContext.sessionTopics)
        .sort(([_, a], [__, b]) => b - a)
        .map(([topic]) => topic)[0] || "";
        
      return `To elaborate further on that: ${getAdditionalDetails(mostFrequentTopic || conversationContext.recentTopics[0] || "")}`;
    }
  }
  
  // Use user preferences to guide the response when context is unclear
  if (conversationContext.userPreferences.length > 0) {
    const userInterest = conversationContext.userPreferences[0];
    return `Based on our conversation, I think you might be interested in more about my ${userInterest}. ${getAdditionalDetails(userInterest)}`;
  }
  
  return null;
};

