// Enhanced search knowledge function with better context awareness
export const searchKnowledge = (query: string): string => {
  const lowerQuery = query.toLowerCase();
  let response: string;
  
  // First check if this is a follow-up question with improved detection
  if (conversationContext.recentQuestions.length > 0 && isFollowUpQuestion(query)) {
    const followUpResponse = processFollowUp(query);
    if (followUpResponse) {
      updateContext(query, followUpResponse);
      return followUpResponse;
    }
  }
  
  // Check for sentiment and conversation patterns
  if (isPositiveSentiment(lowerQuery)) {
    // If user expresses positive sentiment, acknowledge it
    response = `I'm glad you found that helpful! ${suggestRelatedTopicBasedOnHistory()}`;
    updateContext(query, response);
    return response;
  }
  
  if (isNegativeSentiment(lowerQuery)) {
    // If user expresses negative sentiment, adapt
    response = `I apologize if that wasn't what you were looking for. Let me try a different approach. ${suggestAlternativeTopics()}`;
    updateContext(query, response);
    return response;
  }
  
  // Check for meta-conversation (talking about the conversation itself)
  if (lowerQuery.includes("conversation") || lowerQuery.includes("chatbot") || lowerQuery.includes("talking")) {
    const conversationDuration = Math.round((new Date().getTime() - conversationContext.conversationStart.getTime()) / 60000);
    response = `We've been chatting for about ${conversationDuration} minute${conversationDuration !== 1 ? 's' : ''}. I'm here to help you learn more about ${chatbotKnowledge.basics.name}. What else would you like to know?`;
    updateContext(query, response);
    return response;
  }
  
  // Check for greetings
  if (lowerQuery.includes('hello') || lowerQuery.includes('hi') || lowerQuery.includes('hey') || lowerQuery === 'hello') {
    response = `Hello! 👋 I'm an AI assistant that can tell you about ${chatbotKnowledge.basics.name}'s experience, skills, and projects. How can I help you today?`;
    updateContext(query, response);
    return response;
  }
  
  // Check for basic info inquiries
  if (lowerQuery.includes('who are you') || lowerQuery.includes('your name')) {
    response = `I'm ${chatbotKnowledge.basics.name}, a ${chatbotKnowledge.basics.title} based in ${chatbotKnowledge.basics.location}. ${chatbotKnowledge.basics.about}`;
    updateContext(query, response);
    return response;
  }
  
  // Check for about inquiries
  if (lowerQuery.includes('about you') || lowerQuery.includes('tell me about') || (lowerQuery.includes('who') && lowerQuery.includes('you'))) {
    response = chatbotKnowledge.basics.about;
    updateContext(query, response);
    return response;
  }
  
  // Check for skill-related inquiries
  if (lowerQuery.includes('skill') || lowerQuery.includes('technology') || lowerQuery.includes('tech stack') || lowerQuery.includes('what can you do')) {
    // Group skills by category
    const frontendSkills = chatbotKnowledge.skills.slice(0, 8);
    const toolsAndOthers = chatbotKnowledge.skills.slice(8);
    
    response = `Here are my technical skills:\n\n📱 Frontend: ${formatList(frontendSkills)}\n\n🛠 Tools & Specializations: ${formatList(toolsAndOthers)}`;
    updateContext(query, response);
    return response;
  }
  
  // Check for project-related inquiries
  if (lowerQuery.includes('project') || lowerQuery.includes('portfolio') || lowerQuery.includes('work')) {
    const projectSummary = chatbotKnowledge.projects.map(project => 
      `🚀 **${project.name}**\n   ${project.description}\n   Tech: ${project.technologies.join(', ')}`
    ).join('\n\n');
    
    response = `Here are some of my notable projects:\n\n${projectSummary}`;
    updateContext(query, response);
    return response;
  }
  
  // Check for experience-related inquiries
  if (lowerQuery.includes('experience') || lowerQuery.includes('work history') || lowerQuery.includes('job')) {
    const experienceSummary = chatbotKnowledge.experience.map(exp => {
      const responsibilities = exp.responsibilities.map(r => `   • ${r}`).join('\n');
      return `💼 **${exp.position} at ${exp.company}** (${exp.period})\n${responsibilities}`;
    }).join('\n\n');
    
    response = `My professional experience:\n\n${experienceSummary}`;
    updateContext(query, response);
    return response;
  }
  
  // Check for education-related inquiries
  if (lowerQuery.includes('education') || lowerQuery.includes('degree') || lowerQuery.includes('university') || lowerQuery.includes('college')) {
    const educationSummary = chatbotKnowledge.education.map(edu => 
      `🎓 **${edu.degree}**\n   ${edu.institution}, ${edu.year}`
    ).join('\n\n');
    
    response = `My educational background:\n\n${educationSummary}`;
    updateContext(query, response);
    return response;
  }
  
  // Check for contact-related inquiries
  if (lowerQuery.includes('contact') || lowerQuery.includes('email') || lowerQuery.includes('reach you') || lowerQuery.includes('hire')) {
    response = `📧 You can contact me at ${chatbotKnowledge.basics.email} or through the contact form on this website.`;
    updateContext(query, response);
    return response;
  }
  
  // Check for help-related inquiries
  if (lowerQuery.includes('help') || lowerQuery.includes('what can you tell me') || lowerQuery.includes('what can i ask')) {
    response = `I can help you with information about ${chatbotKnowledge.basics.name}. You can ask about:\n\n• Professional skills and technologies\n• Projects and portfolio\n• Work experience\n• Education\n• Contact information\n\nFeel free to ask anything!`;
    updateContext(query, response);
    return response;
  }
  
  // Check against FAQs with improved matching
  let bestFaqMatch = null;
  let bestMatchScore = 0;
  
  for (const faq of chatbotKnowledge.faqs) {
    const similarityScore = calculateSimilarity(query, faq.question);
    if (similarityScore > 0.3 && similarityScore > bestMatchScore) {
      bestMatchScore = similarityScore;
      bestFaqMatch = faq;
    }
  }
  
  if (bestFaqMatch) {
    response = bestFaqMatch.answer;
    updateContext(query, response);
    return response;
  }
  
  // Check for thanks
  if (lowerQuery.includes('thank') || lowerQuery.includes('thanks') || lowerQuery.includes('thank you')) {
    response = "You're welcome! Is there anything else you'd like to know?";
    updateContext(query, response);
    return response;
  }
  
  // Check for goodbye
  if (lowerQuery.includes('bye') || lowerQuery.includes('goodbye') || lowerQuery.includes('see you')) {
    response = "Goodbye! Feel free to come back if you have more questions!";
    updateContext(query, response);
    return response;
  }
  
  // Handle complex questions by identifying multiple topics with improved matching
  const topics = identifyTopics(query);
  if (topics.length > 1) {
    // This is a multi-topic question, try to address all mentioned topics
    const responses: string[] = [];
    
    for (const topic of topics) {
      const specificResponse = searchSpecificTopic(query, topic);
      if (specificResponse) {
        responses.push(specificResponse);
      }
    }
    
    if (responses.length > 0) {
      response = responses.join("\n\n");
      updateContext(query, response);
      return response;
    }
  }
  
  // Add personalized suggestions based on conversation history
  const suggestedTopics = getSuggestedTopicsBasedOnHistory();
  const suggestions = suggestedTopics.map(topic => `• Ask about my ${topic}`).join('\n');
  
  // Improved default response with personalization
  response = `I don't have specific information about that. Based on our conversation, you might want to try:\n\n${suggestions}\n\nOr check the relevant sections of the portfolio for more details.`;
  updateContext(query, response);
  return response;
};

// Helper functions for enhanced sentiment analysis
const isPositiveSentiment = (text: string): boolean => {
  const positiveWords = ['good', 'great', 'excellent', 'awesome', 'amazing', 'thanks', 'thank you', 'helpful', 'useful', 'nice', 'appreciate'];
  return positiveWords.some(word => text.includes(word));
};

const isNegativeSentiment = (text: string): boolean => {
  const negativeWords = ['bad', 'wrong', 'incorrect', 'not helpful', 'don\'t understand', 'confused', 'confusing', 'no', 'not what I asked'];
  return negativeWords.some(word => text.includes(word));
};

// Get suggested topics based on conversation history
const getSuggestedTopicsBasedOnHistory = (): string[] => {
  // If user has shown interest in certain topics, suggest related ones
  if (conversationContext.userPreferences.length > 0) {
    const primaryInterest = conversationContext.userPreferences[0];
    
    // Suggest related topics
    if (primaryInterest === 'skills') {
      return ['projects', 'experience'];
    } else if (primaryInterest === 'projects') {
      return ['skills', 'experience'];
    } else if (primaryInterest === 'experience') {
      return ['skills', 'education'];
    } else if (primaryInterest === 'education') {
      return ['experience', 'skills'];
    } else {
      return ['skills', 'projects', 'experience'].filter(t => t !== primaryInterest);
    }
  }
  
  // Default suggestions if no clear preferences
  return ['skills', 'projects', 'experience'];
};

// Suggest related topics based on conversation history
const suggestRelatedTopicBasedOnHistory = (): string => {
  if (conversationContext.recentTopics.length > 0) {
    const lastTopic = conversationContext.recentTopics[0];
    
    if (lastTopic === 'skills') {
      return "Would you like to know how I've applied these skills in my projects?";
    } else if (lastTopic === 'projects') {
      return "Would you like to know more about the specific technologies I used in these projects?";
    } else if (lastTopic === 'experience') {
      return "Would you like to know more about my educational background or specific skills?";
    } else if (lastTopic === 'education') {
      return "Would you like to know how my education has influenced my professional work?";
    }
  }
  
  return "What else would you like to know about me?";
};

// Suggest alternative topics when user seems unsatisfied
const suggestAlternativeTopics = (): string => {
  // Avoid topics user has already asked about
  const availableTopics = Object.keys(chatbotKnowledge.topics)
    .filter(topic => !conversationContext.recentTopics.includes(topic));
  
  if (availableTopics.length > 0) {
    const suggestions = availableTopics.slice(0, 2);
    return `Perhaps you'd be interested in my ${suggestions.join(' or ')} instead?`;
  }
  
  return "Can you try rephrasing your question? I want to make sure I understand what you're looking for.";
}; 