import React, { useState, useRef, useEffect } from 'react';
import Section from '../components/Section';
import Button from '../components/Button';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { FiMail, FiPhone, FiMapPin, FiSend, FiLinkedin, FiGithub } from 'react-icons/fi';
import emailjs from 'emailjs-com';
import PageBackground from '../components/PageBackground';
import { useMobile } from '../hooks/useMediaQuery';
import { Link } from 'react-router-dom';
import { calculateExperienceYears, CAREER_START } from '../utils/experienceUtils';

const ContactPage: React.FC = () => {
  const formRef = useRef<HTMLFormElement>(null);
  const [formState, setFormState] = useState({
    from_name: '',
    reply_to: '',
    subject: '',
    message: '',
  });
  
  // Use mobile detection hook
  const isMobile = useMobile();
  
  // Calculate years of experience dynamically
  const experienceYears = calculateExperienceYears(CAREER_START.year, CAREER_START.month);
  
  useEffect(() => {
    // Initialize EmailJS with your public key
    emailjs.init("mitFBwd43Ky99sQf6");
  }, []);
  
  const [formStatus, setFormStatus] = useState<'idle' | 'submitting' | 'success' | 'error'>('idle');
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormState((prev) => ({ ...prev, [name]: value }));
  };
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validate required fields
    if (!formState.from_name || !formState.reply_to || !formState.subject || !formState.message) {
      setFormStatus('error');
      setTimeout(() => {
        setFormStatus('idle');
      }, 3000);
      return;
    }
    
    setFormStatus('submitting');
    
    const serviceId = 'service_aadmhtt';
    const templateId = 'template_bcmc59t';
    
    if (!formRef.current) return;
    
    // Use send method instead of sendForm
    // @ts-ignore
    emailjs.sendForm(serviceId,
      templateId,
      formRef.current,
      "mitFBwd43Ky99sQf6")
      .then((result) => {
        console.log('Email successfully sent!', result.text);
        setFormStatus('success');
        setFormState({
          from_name: '',
          reply_to: '',
          subject: '',
          message: '',
        });
        
        // Reset form status after 3 seconds
        setTimeout(() => {
          setFormStatus('idle');
        }, 3000);
      })
      .catch((error) => {
        console.error('Failed to send email:', error);
        setFormStatus('error');
        
        // Reset form status after 3 seconds
        setTimeout(() => {
          setFormStatus('idle');
        }, 3000);
      });
  };
  
  return (
    <ContactContainer>
      <PageBackground variant="contact" />
      
      {/* Recruiters CTA Section - Moved to the top */}
      <RecruitersCTASection>
        <CTAContainer>
          <RecruiterHeading>
            Need a <span>Frontend Engineer</span> for Your Team?
          </RecruiterHeading>
          <RecruiterSubheading>
            I'm available for interviews and can respond quickly. With {experienceYears} years of experience 
            building high-performance web applications, I can help bring your projects to life.
          </RecruiterSubheading>
          <ButtonGroup>
            <ResumeButton 
              href="/files/Suren's Resume - Frontend Software Engineer.pdf" 
              target="_blank" 
              rel="noopener noreferrer"
            >
              View My Resume
            </ResumeButton>
            <CalendlyButton 
              href="https://calendly.com/surhak"
              target="_blank" 
              rel="noopener noreferrer"
            >
              Schedule a Call
            </CalendlyButton>
          </ButtonGroup>
        </CTAContainer>
      </RecruitersCTASection>
      
      <Section
        id="contact"
        title="Get In Touch"
        subtitle="Have a question or want to work together? Reach out to me!"
      >
        <ContactContent>
          <ContactInfo
            as={motion.div}
            initial={{ opacity: 0, x: isMobile ? -15 : -30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: isMobile ? 0.4 : 0.5 }}
          >
            <ContactHeading>Contact Information</ContactHeading>
            <ContactDescription>
              Fill out the form or get in touch directly using the information below.
            </ContactDescription>
            
            <ContactMethods>
              <ContactMethod>
                <ContactIcon>
                  <FiMail size={isMobile ? 18 : 20} />
                </ContactIcon>
                <ContactMethodContent>
                  <ContactMethodLabel>Email</ContactMethodLabel>
                  <ContactMethodValue>
                    <ContactLink href="mailto:surhak96@gmail.com">
                      surhak96@gmail.com
                    </ContactLink>
                  </ContactMethodValue>
                </ContactMethodContent>
              </ContactMethod>
              
              <ContactMethod>
                <ContactIcon>
                  <FiPhone size={isMobile ? 18 : 20} />
                </ContactIcon>
                <ContactMethodContent>
                  <ContactMethodLabel>Phone</ContactMethodLabel>
                  <ContactMethodValue>
                    <ContactLink href="tel:+37495222706">
                      +374 95 222 706
                    </ContactLink>
                  </ContactMethodValue>
                </ContactMethodContent>
              </ContactMethod>
              
              <ContactMethod>
                <ContactIcon>
                  <FiMapPin size={isMobile ? 18 : 20} />
                </ContactIcon>
                <ContactMethodContent>
                  <ContactMethodLabel>Location</ContactMethodLabel>
                  <ContactMethodValue>
                    <ContactLink href="https://www.google.com/maps/place/Yerevan,+Armenia" target="_blank" rel="noopener noreferrer">
                      Yerevan, Armenia
                    </ContactLink>
                  </ContactMethodValue>
                </ContactMethodContent>
              </ContactMethod>
            </ContactMethods>
            
            <SocialLinks>
              <SocialLink href="https://linkedin.com/in/surhak" target="_blank" rel="noopener noreferrer">
                <FiLinkedin size={isMobile ? 18 : 20} />
              </SocialLink>
              <SocialLink href="https://github.com/surohak" target="_blank" rel="noopener noreferrer">
                <FiGithub size={isMobile ? 18 : 20} />
              </SocialLink>
              <SocialLink href="https://t.me/surohak" target="_blank" rel="noopener noreferrer">
                <FiSend size={isMobile ? 18 : 20} />
              </SocialLink>
            </SocialLinks>
          </ContactInfo>
          
          <ContactForm
            ref={formRef}
            as={motion.form}
            initial={{ opacity: 0, x: isMobile ? 15 : 30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: isMobile ? 0.4 : 0.5 }}
            onSubmit={handleSubmit}
          >
            <FormRow>
              <FormGroup>
                <FormLabel htmlFor="from_name">Name</FormLabel>
                <FormInput
                  type="text"
                  id="from_name"
                  name="from_name"
                  value={formState.from_name}
                  onChange={handleChange}
                  required
                  placeholder="Your Name"
                />
              </FormGroup>
              
              <FormGroup>
                <FormLabel htmlFor="reply_to">Email</FormLabel>
                <FormInput
                  type="email"
                  id="reply_to"
                  name="reply_to"
                  value={formState.reply_to}
                  onChange={handleChange}
                  required
                  placeholder="Your Email"
                />
              </FormGroup>
            </FormRow>
            
            <FormGroup>
              <FormLabel htmlFor="subject">Subject</FormLabel>
              <FormInput
                type="text"
                id="subject"
                name="subject"
                value={formState.subject}
                onChange={handleChange}
                required
                placeholder="Subject"
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel htmlFor="message">Message</FormLabel>
              <FormTextarea
                id="message"
                name="message"
                value={formState.message}
                onChange={handleChange}
                required
                placeholder="Your Message"
                rows={isMobile ? 4 : 5}
              />
            </FormGroup>
            
            <FormButton>
              <Button
                type="submit"
                variant="primary"
                size={isMobile ? "medium" : "large"}
                icon={<FiSend />}
                disabled={formStatus === 'submitting'}
                fullWidth
              >
                {formStatus === 'submitting' ? 'Sending...' : 'Send Message'}
              </Button>
            </FormButton>
            
            {formStatus === 'success' && (
              <SuccessMessage
                as={motion.div}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: isMobile ? 0.3 : 0.4 }}
                exit={{ opacity: 0, y: 10 }}
              >
                Your message has been sent successfully!
              </SuccessMessage>
            )}
            
            {formStatus === 'error' && (
              <ErrorMessage
                as={motion.div}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: isMobile ? 0.3 : 0.4 }}
                exit={{ opacity: 0, y: 10 }}
              >
                There was an error sending your message. Please try again.
              </ErrorMessage>
            )}
          </ContactForm>
        </ContactContent>
      </Section>
    </ContactContainer>
  );
};

const ContactContainer = styled.div`
  padding-top: 100px;
  
  @media (max-width: 576px) {
    padding-top: 80px;
  }
`;

