import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { motion, useTransform, useScroll } from 'framer-motion';
import { useMobile } from '../hooks/useMediaQuery';

interface PageBackgroundProps {
  variant?: 'about' | 'projects' | 'skills' | 'contact';
}

const PageBackground: React.FC<PageBackgroundProps> = ({ variant = 'about' }) => {
  const [windowHeight, setWindowHeight] = useState(0);
  const { scrollY } = useScroll();
  const isMobile = useMobile();
  
  // Set the window height on mount for scroll calculations
  useEffect(() => {
    const updateDimensions = () => {
      setWindowHeight(window.innerHeight);
    };
    
    updateDimensions();
    window.addEventListener('resize', updateDimensions);
    return () => window.removeEventListener('resize', updateDimensions);
  }, []);
  
  // Create scroll-driven transforms for the gradient blobs
  // Use less complex transforms on mobile
  const rotateBlob1 = useTransform(scrollY, [0, windowHeight * 2], [0, isMobile ? 180 : 360]);
  const translateXBlob1 = useTransform(scrollY, [0, windowHeight], [0, isMobile ? 25 : 50]);
  const translateYBlob1 = useTransform(scrollY, [0, windowHeight], [0, isMobile ? -15 : -30]);
  const scaleBlob1 = useTransform(scrollY, [0, windowHeight * 1.5], [1, isMobile ? 1.1 : 1.2]);
  
  const rotateBlob2 = useTransform(scrollY, [0, windowHeight * 2], [0, isMobile ? -180 : -360]);
  const translateXBlob2 = useTransform(scrollY, [0, windowHeight], [0, isMobile ? -20 : -40]);
  const translateYBlob2 = useTransform(scrollY, [0, windowHeight], [0, isMobile ? 10 : 20]);
  const scaleBlob2 = useTransform(scrollY, [0, windowHeight * 1.5], [1, isMobile ? 0.9 : 0.8]);
  
  const gridOpacity = useTransform(scrollY, [0, windowHeight], [0.01, 0.02]);
  
  // Define different colors for different page types
  const getColor = () => {
    switch (variant) {
      case 'about':
        return 'var(--accent)';
      case 'projects':
        return '#6366f1'; // Indigo
      case 'skills':
        return '#10b981'; // Emerald
      case 'contact':
        return '#f59e0b'; // Amber
      default:
        return 'var(--accent)';
    }
  };

  return (
    <AnimationContainer>
      <GradientBlob 
        style={{ 
          rotate: rotateBlob1,
          x: translateXBlob1,
          y: translateYBlob1,
          scale: scaleBlob1,
        }}
        animate={isMobile ? {
          filter: ["blur(80px)", "blur(90px)"],
        } : {
          filter: ["blur(100px)", "blur(120px)", "blur(90px)"],
        }}
        transition={{
          filter: {
            duration: isMobile ? 4 : 5,
            repeat: Infinity,
            repeatType: 'reverse',
          }
        }}
        blurRadius={isMobile ? 80 : 100}
        color={getColor()}
        opacity={0.04}
        size={isMobile ? 400 : 500}
        top="5%"
        right="10%"
      />
      {/* Conditionally render second blob based on device */}
      {!isMobile && (
        <GradientBlob 
          style={{ 
            rotate: rotateBlob2,
            x: translateXBlob2,
            y: translateYBlob2,
            scale: scaleBlob2,
          }}
          animate={{
            filter: ["blur(120px)", "blur(100px)", "blur(140px)"],
          }}
          transition={{
            filter: {
              duration: 8,
              repeat: Infinity,
              repeatType: 'reverse',
            }
          }}
          blurRadius={120}
          color={getColor()}
          opacity={0.03}
          size={600}
          bottom="10%"
          left="5%"
        />
      )}
      <GridPattern style={{ opacity: gridOpacity }} />
    </AnimationContainer>
  );
};

const AnimationContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
`;

interface GradientBlobProps {
  blurRadius: number;
  color: string;
  opacity: number;
  size: number;
  top?: string;
  left?: string;
  right?: string;
  bottom?: string;
}

const GradientBlob = styled(motion.div)<GradientBlobProps>`
  position: absolute;
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  border-radius: 50%;
  background-color: ${props => props.color};
  opacity: ${props => props.opacity};
  filter: blur(${props => props.blurRadius}px);
  top: ${props => props.top || 'auto'};
  left: ${props => props.left || 'auto'};
  right: ${props => props.right || 'auto'};
  bottom: ${props => props.bottom || 'auto'};
  z-index: 0;
  transform: translateZ(0);
  will-change: transform;
`;

const GridPattern = styled(motion.div)`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(var(--border) 1px, transparent 1px),
    linear-gradient(90deg, var(--border) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: 0.015;
  z-index: 1;
`;

export default PageBackground; 