const ProjectsPage: React.FC = () => {
  // Memoize the initial category filtering to prevent recreation on every render
  const professionalProjects = useMemo(() => 
    projectsData.filter(project => project.category === 'professional'),
    [/* No dependencies, only calculate once */]
  );
  
  const freelanceProjects = useMemo(() => 
    projectsData.filter(project => project.category === 'freelance'),
    [/* No dependencies, only calculate once */]
  );
  
  const [loadedImages, setLoadedImages] = useState<{[key: string]: boolean}>({});
  const [loadedHighResImages, setLoadedHighResImages] = useState<{[key: string]: boolean}>({});
  const isMobile = useMobile(992);
  const [visibleProjects, setVisibleProjects] = useState<{[key: number]: boolean}>({});
  const observers = useRef<{[key: number]: IntersectionObserver | null}>({});
  
  // Add search state
  const [searchQuery, setSearchQuery] = useState<string>('');
  
  // Memoize the filtered projects based on search
  const filteredProfessional = useMemo(() => {
    if (!searchQuery.trim()) return professionalProjects;
    
    const query = searchQuery.toLowerCase();
    return professionalProjects.filter(project => 
      project.title.toLowerCase().includes(query) || 
      project.description.toLowerCase().includes(query) ||
      project.technologies.some(tech => tech.toLowerCase().includes(query)) ||
      (project.company && project.company.toLowerCase().includes(query))
    );
  }, [searchQuery, professionalProjects]);
  
  const filteredFreelance = useMemo(() => {
    if (!searchQuery.trim()) return freelanceProjects;
    
    const query = searchQuery.toLowerCase();
    return freelanceProjects.filter(project => 
      project.title.toLowerCase().includes(query) || 
      project.description.toLowerCase().includes(query) ||
      project.technologies.some(tech => tech.toLowerCase().includes(query)) ||
      (project.company && project.company.toLowerCase().includes(query))
    );
  }, [searchQuery, freelanceProjects]);

  // Initialize observers and preload images
  useEffect(() => {
    // Preload first few images
    const preloadCount = isMobile ? 3 : 4;
    const imagesToPreload = [...professionalProjects.slice(0, preloadCount)];
    
    // Mark the first few projects as visible by default
    const initialVisibleProjects = imagesToPreload.reduce((acc, project) => {
      acc[project.id] = true;
      return acc;
    }, {} as {[key: number]: boolean});
    
    setVisibleProjects(initialVisibleProjects);
    
    // Preload small versions
    imagesToPreload.forEach(project => {
      const img = new Image();
      img.src = getSmallImagePath(project.image);
      img.onload = () => handleImageLoaded(project.image);
    });
    
    return () => {
      // Clean up observers
      Object.values(observers.current).forEach(observer => {
        if (observer) observer.disconnect();
      });
    };
  }, [isMobile, professionalProjects]);

  // Get smaller version of the image for initial loading
  const getSmallImagePath = (path: string) => {
    const isExternal = path.startsWith('http');
    if (isExternal) return path;
    
    // Add query parameters for smaller size - adjust size for mobile
    return `${path}?w=${isMobile ? 150 : 200}&q=60`;
  };
  
  // Load high-res version after small version loads
  const loadHighResImage = (src: string) => {
    if (loadedHighResImages[src]) return;
    
    const highResImg = new Image();
    highResImg.src = src;
    highResImg.onload = () => {
      setLoadedHighResImages(prev => ({
        ...prev,
        [src]: true
      }));
    };
  };

  const handleImageLoaded = (src: string) => {
    setLoadedImages(prev => ({
      ...prev,
      [src]: true
    }));
    
    // Start loading high-res version after a short delay
    // This helps the initial load appear smoother
    setTimeout(() => {
      loadHighResImage(src);
    }, 300);
  };

  // Observer callback for intersection events
  const observerCallback = useCallback((entries: IntersectionObserverEntry[], projectId: number) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        setVisibleProjects(prev => ({
          ...prev,
          [projectId]: true
        }));
        
        // Disconnect observer after project becomes visible
        if (observers.current[projectId]) {
          observers.current[projectId]?.disconnect();
          observers.current[projectId] = null;
        }
      }
    });
  }, []);

  // Ref callback to observe project cards
  const projectRef = useCallback((node: HTMLDivElement | null, projectId: number) => {
    if (node && !visibleProjects[projectId]) {
      // Clean up previous observer
      if (observers.current[projectId]) {
        observers.current[projectId]?.disconnect();
      }
      
      // Create new observer with adjusted thresholds for mobile
      observers.current[projectId] = new IntersectionObserver(
        entries => observerCallback(entries, projectId),
        { 
          threshold: isMobile ? 0.05 : 0.1, 
          rootMargin: isMobile ? '200px' : '100px'
        }
      );
      
      observers.current[projectId]?.observe(node);
    }
  }, [visibleProjects, observerCallback, isMobile]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: isMobile ? 0.05 : 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: isMobile ? 0.3 : 0.4,
      },
    },
  };

  const renderProjects = (projects: Project[]) => (
    <ProjectsGrid
      as={motion.div}
      variants={containerVariants}
      initial="hidden"
      animate="visible"
    >
      {projects.map((project, index) => (
        <ProjectCard
          key={project.id}
          as={motion.div}
          variants={itemVariants}
          ref={node => projectRef(node, project.id)}
          onClick={() => project.liveLink && window.open(project.liveLink, '_blank')}
          style={{ cursor: project.liveLink ? 'pointer' : 'default' }}
        >
          <ProjectImageContainer>
            {!loadedImages[project.image] && (
              <ImagePlaceholder fadeOut={loadedImages[project.image]}>
                <PlaceholderText>{project.title}</PlaceholderText>
                <LoadingBar><LoadingProgress /></LoadingBar>
              </ImagePlaceholder>
            )}
            <ProjectImage 
              src={loadedHighResImages[project.image] ? project.image : getSmallImagePath(project.image)}
              alt={project.title}
              loading="lazy"
              decoding="async"
              onLoad={() => handleImageLoaded(project.image)}
              style={{ 
                opacity: loadedImages[project.image] ? 1 : 0,
                filter: loadedHighResImages[project.image] ? 'blur(0)' : 'blur(5px)',
                transform: `scale(${loadedHighResImages[project.image] ? '1' : '1.05'})`
              }}
              data-priority={index < 3}
            />
            <ProjectOverlay 
              visible={loadedHighResImages[project.image]} 
              style={{ opacity: loadedHighResImages[project.image] ? 1 : 0 }}
            >
              <ProjectLinks onClick={(e) => e.stopPropagation()}>
                {project.githubLink && (
                  <ProjectLink href={project.githubLink} target="_blank" rel="noopener noreferrer" title="GitHub Repository">
                    <Icon icon={FiGithub} size={18} />
                  </ProjectLink>
                )}
                {project.liveLink && (
                  <ProjectLink href={project.liveLink} target="_blank" rel="noopener noreferrer" title="Live Website">
                    <Icon icon={FiExternalLink} size={18} />
                  </ProjectLink>
                )}
              </ProjectLinks>
            </ProjectOverlay>
          </ProjectImageContainer>
          <ProjectContent>
            <ProjectHeader>
              <ProjectTitle>{project.title}</ProjectTitle>
              <CompanyInfo>
                {project.position && project.company ? 
                  `${project.position} at ${project.company}` :
                  project.position || ''}
              </CompanyInfo>
            </ProjectHeader>
            <ProjectDescription>{project.description}</ProjectDescription>
            <TechStack>
              {project.technologies.map((tech, i) => (
                <TechTag key={i}>{tech}</TechTag>
              ))}
            </TechStack>
          </ProjectContent>
        </ProjectCard>
      ))}
    </ProjectsGrid>
  );

  return (
    <ProjectsPageContainer>
      <PageBackground variant="projects" />
      <Section
        id="projects"
        title="Projects"
        subtitle="A selection of my recent work and contributions"
      >
        {/* Add search bar */}
        <SearchContainer>
          <SearchWrapper>
            <SearchIcon>
              <FiSearch />
            </SearchIcon>
            <SearchInput 
              type="text" 
              placeholder="Search projects by title, technology, or description..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
            {searchQuery && (
              <ClearButton
                onClick={() => setSearchQuery('')}
                aria-label="Clear search"
              >
                ✕
              </ClearButton>
            )}
          </SearchWrapper>
        </SearchContainer>

        {/* Always render the SearchResults container to prevent layout shifts */}
        <SearchResults>
          {searchQuery ? 
            `Found ${filteredProfessional.length + filteredFreelance.length} projects matching "${searchQuery}"` 
            : '\u00A0'} {/* Non-breaking space to maintain height when empty */}
        </SearchResults>

        {/* Only show Professional section if there are matching projects or no search */}
        {(filteredProfessional.length > 0 || !searchQuery) && (
          <SectionDivider>
            <SectionTitle>Professional Work</SectionTitle>
            {renderProjects(filteredProfessional)}
          </SectionDivider>
        )}
        
        {/* Only show Freelance section if there are matching projects or no search */}
        {(filteredFreelance.length > 0 || !searchQuery) && (
          <SectionDivider>
            <SectionTitle>Freelance Projects</SectionTitle>
            {renderProjects(filteredFreelance)}
          </SectionDivider>
        )}

        {/* Show no results message when needed */}
        {searchQuery && filteredProfessional.length === 0 && filteredFreelance.length === 0 && (
          <NoResults>
            No projects found matching "{searchQuery}". Try a different search term.
          </NoResults>
        )}
      </Section>
    </ProjectsPageContainer>
  );
};

