import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

type BaseTheme = 'light' | 'dark';
type Theme = BaseTheme | 'custom';

// Define the structure for custom theme properties
export interface CustomThemeProperties {
  bgPrimary: string;
  bgSecondary: string;
  textPrimary: string;
  textSecondary: string;
  accent: string;
  accentHover: string;
  border: string;
  shadow: string;
  fontFamily: string;
}

interface ThemeContextType {
  theme: Theme;
  toggleTheme: () => void;
  setCustomTheme: (properties: CustomThemeProperties) => void;
  resetToDefaultTheme: () => void;
  customTheme: CustomThemeProperties | null;
}

// Default theme values
const defaultCustomTheme: CustomThemeProperties = {
  bgPrimary: '#f8f9fa',
  bgSecondary: '#ffffff',
  textPrimary: '#212529',
  textSecondary: '#495057',
  accent: '#0066ff',
  accentHover: '#0055d4',
  border: '#dee2e6',
  shadow: 'rgba(0, 0, 0, 0.1)',
  fontFamily: "'Inter', sans-serif"
};

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

interface ThemeProviderProps {
  children: ReactNode;
}

export const ThemeProvider: React.FC<ThemeProviderProps> = ({ children }) => {
  // Function to detect system color scheme preference
  const getSystemTheme = (): BaseTheme => {
    return window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches
      ? 'dark'
      : 'light';
  };

  const [theme, setTheme] = useState<Theme>(() => {
    // Check if theme is stored in localStorage
    const savedTheme = localStorage.getItem('theme') as Theme;
    // Use saved theme or fallback to system preference
    return savedTheme || getSystemTheme();
  });

  const [customTheme, setCustomTheme] = useState<CustomThemeProperties | null>(() => {
    // Check if custom theme is stored in localStorage
    const savedCustomTheme = localStorage.getItem('customTheme');
    // Parse saved theme if exists
    return savedCustomTheme ? JSON.parse(savedCustomTheme) : null;
  });

  // Apply base theme to body
  useEffect(() => {
    document.body.dataset.theme = theme;
    localStorage.setItem('theme', theme);
  }, [theme]);

  // Apply custom theme properties
  useEffect(() => {
    if (theme === 'custom' && customTheme) {
      // Generate RGB values for accent colors
      const accentRGB = hexToRgb(customTheme.accent);
      const accentHoverRGB = hexToRgb(customTheme.accentHover);
      
      // Apply custom theme CSS variables
      document.documentElement.style.setProperty('--custom-bg-primary', customTheme.bgPrimary);
      document.documentElement.style.setProperty('--custom-bg-secondary', customTheme.bgSecondary);
      document.documentElement.style.setProperty('--custom-text-primary', customTheme.textPrimary);
      document.documentElement.style.setProperty('--custom-text-secondary', customTheme.textSecondary);
      document.documentElement.style.setProperty('--custom-accent', customTheme.accent);
      document.documentElement.style.setProperty('--custom-accent-hover', customTheme.accentHover);
      document.documentElement.style.setProperty('--custom-border', customTheme.border);
      document.documentElement.style.setProperty('--custom-shadow', customTheme.shadow);
      document.documentElement.style.setProperty('--custom-accent-rgb', accentRGB);
      document.documentElement.style.setProperty('--custom-accent-hover-rgb', accentHoverRGB);
      
      // Apply custom font if provided
      if (customTheme.fontFamily) {
        document.documentElement.style.setProperty('--custom-font-family', customTheme.fontFamily);
      }
    } else {
      // Reset custom properties if not using custom theme
      document.documentElement.style.removeProperty('--custom-bg-primary');
      document.documentElement.style.removeProperty('--custom-bg-secondary');
      document.documentElement.style.removeProperty('--custom-text-primary');
      document.documentElement.style.removeProperty('--custom-text-secondary');
      document.documentElement.style.removeProperty('--custom-accent');
      document.documentElement.style.removeProperty('--custom-accent-hover');
      document.documentElement.style.removeProperty('--custom-border');
      document.documentElement.style.removeProperty('--custom-shadow');
      document.documentElement.style.removeProperty('--custom-accent-rgb');
      document.documentElement.style.removeProperty('--custom-accent-hover-rgb');
      document.documentElement.style.removeProperty('--custom-font-family');
    }
  }, [theme, customTheme]);

  // Listen for system theme changes
  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    
    const handleThemeChange = (e: MediaQueryListEvent) => {
      // Only update theme if user hasn't set a preference
      if (!localStorage.getItem('theme')) {
        setTheme(e.matches ? 'dark' : 'light');
      }
    };
    
    // Add listener for theme changes
    if (mediaQuery.addEventListener) {
      mediaQuery.addEventListener('change', handleThemeChange);
    } else {
      // Fallback for older browsers
      mediaQuery.addListener(handleThemeChange);
    }
    
    // Cleanup
    return () => {
      if (mediaQuery.removeEventListener) {
        mediaQuery.removeEventListener('change', handleThemeChange);
      } else {
        // Fallback for older browsers
        mediaQuery.removeListener(handleThemeChange);
      }
    };
  }, []);

  // Toggle between light and dark mode
  const toggleTheme = () => {
    setTheme((prevTheme) => {
      // Only toggle between light and dark
      let newTheme: Theme;
      if (prevTheme === 'custom') {
        newTheme = 'light';
      } else {
        newTheme = prevTheme === 'light' ? 'dark' : 'light';
      }
      // Save user preference
      localStorage.setItem('theme', newTheme);
      return newTheme;
    });
  };

  // Set custom theme
  const handleSetCustomTheme = (properties: CustomThemeProperties) => {
    setCustomTheme(properties);
    setTheme('custom');
    localStorage.setItem('customTheme', JSON.stringify(properties));
    localStorage.setItem('theme', 'custom');
  };

  // Reset to default theme (light or dark)
  const resetToDefaultTheme = () => {
    const defaultTheme = getSystemTheme();
    setTheme(defaultTheme);
    localStorage.setItem('theme', defaultTheme);
  };

  return (
    <ThemeContext.Provider value={{ 
      theme, 
      toggleTheme, 
      setCustomTheme: handleSetCustomTheme, 
      resetToDefaultTheme,
      customTheme
    }}>
      {children}
    </ThemeContext.Provider>
  );
};

// Helper function to convert hex color to RGB
function hexToRgb(hex: string): string {
  // Remove # if present
  hex = hex.replace('#', '');
  
  // Parse hex values
  const r = parseInt(hex.substring(0, 2), 16);
  const g = parseInt(hex.substring(2, 4), 16);
  const b = parseInt(hex.substring(4, 6), 16);
  
  // Return RGB string
  return `${r}, ${g}, ${b}`;
}

// Custom hook to use the theme context
export const useTheme = (): ThemeContextType => {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error('useTheme must be used within a ThemeProvider');
  }
  return context;
}; 