import React from 'react';
import { IconType } from 'react-icons';

interface IconProps {
  icon: IconType;
  size?: number | string;
  color?: string;
  className?: string;
}

const Icon: React.FC<IconProps> = ({ icon: IconComponent, size, color, className }) => {
  return <IconComponent size={size} color={color} className={className} />;
};

export default Icon; 