import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { useMobile } from '../hooks/useMediaQuery';

const BackgroundAnimation: React.FC = () => {
  // Use mobile detection hook
  const isMobile = useMobile();

  return (
    <AnimationContainer>
      <GradientBlob 
        animate={!isMobile ? {
          x: [0, 50, -20, 0],
          y: [0, -50, 20, 0],
          scale: [1, 1.1, 0.9, 1],
          rotate: [0, 90, 180, 270, 360],
        } : {
          // Simplified animation for mobile
          x: [0, 20, 0],
          y: [0, -20, 0],
          scale: [1, 1.05, 1],
        }}
        transition={{
          duration: !isMobile ? 25 : 20,
          repeat: Infinity,
          repeatType: 'loop',
          // Add will-change optimization
          willChange: "transform",
        }}
        blurRadius={isMobile ? 60 : 80}
        color="var(--accent)"
        opacity={0.08}
        size={isMobile ? 400 : 600}
        top="10%"
        left="5%"
      />
      <GradientBlob 
        animate={!isMobile ? {
          x: [0, -30, 60, 0],
          y: [0, 40, -60, 0],
          scale: [1, 0.9, 1.2, 1],
          rotate: [0, -90, -180, -270, -360],
        } : {
          // Simplified animation for mobile
          x: [0, -15, 0],
          y: [0, 15, 0],
          scale: [1, 0.95, 1],
        }}
        transition={{
          duration: !isMobile ? 30 : 18,
          repeat: Infinity,
          repeatType: 'loop',
          // Add will-change optimization
          willChange: "transform",
        }}
        blurRadius={isMobile ? 70 : 90}
        color="var(--accent-hover)"
        opacity={0.06}
        size={isMobile ? 350 : 500}
        top="50%"
        right="10%"
      />
      {/* Remove third blob on mobile for better performance */}
      {!isMobile && (
        <GradientBlob 
          animate={{
            x: [0, 40, -30, 0],
            y: [0, -30, -60, 0],
            scale: [1, 1.2, 0.8, 1],
            rotate: [0, 60, 120, 180, 240, 300, 360],
          }}
          transition={{
            duration: 20,
            repeat: Infinity,
            repeatType: 'loop',
            willChange: "transform",
          }}
          blurRadius={70}
          color="var(--accent)"
          opacity={0.05}
          size={450}
          bottom="5%"
          left="30%"
        />
      )}
      <GridPattern opacity={isMobile ? 0.01 : 0.02} />
    </AnimationContainer>
  );
};

const AnimationContainer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
`;

interface GradientBlobProps {
  blurRadius: number;
  color: string;
  opacity: number;
  size: number;
  top?: string;
  left?: string;
  right?: string;
  bottom?: string;
}

// Optimize the blob with hardware acceleration
const GradientBlob = styled(motion.div)<GradientBlobProps>`
  position: absolute;
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  border-radius: 50%;
  background-color: ${props => props.color};
  opacity: ${props => props.opacity};
  filter: blur(${props => props.blurRadius}px);
  top: ${props => props.top || 'auto'};
  left: ${props => props.left || 'auto'};
  right: ${props => props.right || 'auto'};
  bottom: ${props => props.bottom || 'auto'};
  z-index: 0;
  transform: translateZ(0);
  will-change: transform;
`;

interface GridPatternProps {
  opacity: number;
}

const GridPattern = styled.div<GridPatternProps>`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(var(--border) 1px, transparent 1px),
    linear-gradient(90deg, var(--border) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: ${props => props.opacity};
  z-index: 1;
`;

export default BackgroundAnimation; 