// Function to calculate years of experience from a start date
export const calculateExperienceYears = (startYear: number, startMonth: number): string => {
  const startDate = new Date(startYear, startMonth - 1); // months are 0-indexed in JS
  const currentDate = new Date();
  
  let years = currentDate.getFullYear() - startDate.getFullYear();
  
  // Adjust if we haven't reached the anniversary month yet
  if (
    currentDate.getMonth() < startDate.getMonth() || 
    (currentDate.getMonth() === startDate.getMonth() && 
     currentDate.getDate() < startDate.getDate())
  ) {
    years--;
  }
  
  return `${years}+`;
};

// The start date of the professional career (e.g., 2018 for year, 9 for September)
export const CAREER_START = {
  year: 2018,
  month: 9
};

// Update the function to include both the start month and end month in the calculation
export const calculateDuration = (startYear: number, startMonth: number, endYear?: number, endMonth?: number): string => {
  const now = new Date();
  const currentYear = endYear || now.getFullYear();
  const currentMonth = endMonth || now.getMonth() + 1; // JavaScript months are 0-indexed
  
  let totalMonths = (currentYear - startYear) * 12 + (currentMonth - startMonth + 1); // Adding 1 to include both start and end months
  
  const years = Math.floor(totalMonths / 12);
  const months = totalMonths % 12;
  
  // Format the output
  if (years === 0) {
    return `${months} ${months === 1 ? 'mo' : 'mos'}`;
  } else if (months === 0) {
    return `${years} ${years === 1 ? 'yr' : 'yrs'}`;
  } else {
    return `${years} ${years === 1 ? 'yr' : 'yrs'} ${months} ${months === 1 ? 'mo' : 'mos'}`;
  }
}; 